.frontis-blocks-quick-access-sidebar-wrapper {
	display: flex;
	height: 100%;
	width: 60px;
	color: #fff;
	background: #1e1e1e;
	flex-direction: column;

	.frontis-blocks-logo {
		margin-top: 15px;
		text-align: center;
	}
}

.frontis-blocks-quick-access-container {
	height: 100%;

	.frontis-blocks-ee-quick-access {
		height: 100%;

		.frontis-blocks-ee-quick-access__sidebar--blocks--block--icon {
			svg path {
				stroke: #ffffff !important;
			}
		}
	}
}



.frontis-blocks-ee-quick-access__sidebar {
	width: 60px;
	display: flex;
	flex-direction: column;
	background: transparent;
	overflow: auto;
	height: 100%;

	&::-webkit-scrollbar {
		width: 0;
		background: transparent;
	}


	.frontis-blocks-ee-quick-access__sidebar--tabs-container {
		.frontis-blocks-ee-quick-access__sidebar--tabs {
			display: flex;
			flex-direction: column;
			.frontis-blocks-sidebar-actions {
				&.active {
					.frontis-blocks-ee-quick-access__sidebar--actions {
						.frontis-blocks-ee-quick-access__sidebar--actions--plus {
							.frontis-blocks-quick-action-sidebar-wrap {
								#plus-icon, #rearrange-icon {
									background-color: #1d2939;
									color: #fff;
								}
							}
						}
					}
				}
				.frontis-blocks-ee-quick-access__sidebar--actions {
					.frontis-blocks-ee-quick-access__sidebar--actions--plus {
						.frontis-blocks-quick-action-sidebar-wrap {
							#plus-icon, #rearrange-icon {
								display: flex;
								align-content: center;
								justify-content: center;
								align-items: center;
								padding: 12px 12px;
								flex-direction: column;
							}
						}
					}
				}
			}
		}
	}

	&--blocks,
	&--actions {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		overflow-y: overlay;
		overflow-x: hidden;
		cursor: pointer;
		max-height: calc(100vh - 212px);
		flex-grow: 1;
		position: relative;
		bottom: 6px;
		left: 0;
		margin-top: 10px;

		&--block {
			display: flex;
			cursor: grab;
			border-radius: 6px;
			gap: 8px;
			padding: 12px;

			&:hover {
				background: #1d2939;
				
				span:not(.dashicon) {
					display: none;
				}
			}

			&--icon {
				display: flex;
				width: 24px;
				height: 24px;
				justify-content: center;
				align-items: center;

				svg path {
					// stroke: #0B0C0E !important;
				}

				&--name {
					display: flex;
					flex-direction: row;
					align-content: center;
					justify-content: center;
					align-items: center;

					.components-popover__content {
						background: none;
						border: none;
						box-shadow: none;
						width: auto;
					}

					.block-title {
						display: flex;
						align-content: center;
						justify-content: center;
						align-items: center;
						font-size: 14px;
						border-radius: 4px;
						margin-right: 20px;
						margin-left: 15px;
						background: #1e1e1e;
						color: #fff;
						padding: 5px 10px;
						cursor: pointer;
					}
				}
			}

			span:not(.dashicon) {
				display: none;
			}
		}
	}
}

.edit-site-layout {
	&.is-full-canvas.is-edit-mode {
		.edit-site-layout__view-mode-toggle.components-button {
			border-bottom: #1e1e1e;
		}
	}
}

.frontis-blocks-quick-action-block-popover {
	margin-left: 15px;

	::-webkit-scrollbar {
		width: 0;
		background: transparent;
	}

	.components-popover__content {
		width: 348px;
		height: 279px;
		background: #fff;
		padding: 16px;
		border-radius: 6px;
		gap: 16px;

		.frontis-blocks-quick-action-block-popover-search {
			input.components-search-control__input {
				width: 316px;
				height: 40px;
				padding: 10px 14px;
				border-radius: 4px;
				border: 1px solid #007cba;
				background: #fff;

				&:focus {
					border: 1px solid #007cba;
					box-shadow: none;
				}

				&::placeholder {
					color: #aaa;
					font-size: 12px;
					font-weight: 500;
				}
			}

			svg {
				fill: #aaa;
			}
		}

		.frontis-blocks-block-container {
			display: flex;
			flex-wrap: wrap;
			overflow: hidden;
			flex-direction: row;
			align-items: stretch;

			.frontis-blocks-quick-action-block-popover-header__already-present-in-quick-action-bar,
			.frontis-blocks-quick-action-block-popover-header__add-to-quick-action-bar {
				width: 316px;
				padding: 16px 0 8px 0;

				.block-editor-inserter__panel-title {
					color: #50575e;
					font-size: 13px;
					font-weight: 400;
					text-transform: capitalize;
				}
			}

			.frontis-blocks-block-wrap {
				margin: 0;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				position: relative;
				transition: all 0.05s ease-in-out;
				align-content: stretch;
				width: 105.33px;
				height: 100px;
				border-radius: 4px;

				.frontis-blocks-ee-quick-access__sidebar--blocks--block--icon {
					width: 64px;
					height: 48px;
					padding: 12px 20px;
					gap: 10px;
				}

				svg.frontis-blocks-editor-icons{
					rect,
					path {
						stroke: #0B0C0E !important;
					}
				}

				.block-title {
					color: #1e1e1e;
					text-align: center;
					width: 80px;
					height: 28px;
					padding: 4px 0 8px 0;
					gap: 10px;
					font-size: 12px;
					line-height: 16px;
					letter-spacing: 0;
					// white-space: nowrap;
					// overflow: hidden;
					// text-overflow: ellipsis;
				}

				&:hover {
					&::after {
						background: #007cba;
						content: "";
						border-radius: 2px;
						bottom: 0;
						left: 0;
						opacity: 0.04;
						pointer-events: none;
						position: absolute;
						right: 0;
						top: 0;
					}

					.block-title {
						color: #007cba;
						filter: brightness(0.95);
					}
				}
			}

			.block-editor-inserter__no-results {
				padding: 50px 105px;
			}
		}
	}
}

.components-popover__fallback-container {
	svg.block-title-svg {
		width: 10px;
		height: 10px;
		fill: #fff;

		&:hover {
			fill: #f00;
		}
	}
}

.edit-site-layout__canvas {
	.spectra-ee-quick-access__sidebar {
		padding-top: 1px;
	}
}

div#widgets-editor {
	.spectra-ee-quick-access__sidebar--blocks,
	.spectra-ee-quick-access__sidebar--actions {
		max-height: calc(100vh - 180px);
	}
}

.spectra-ee-quick-access-container {
	.spectra-ee-quick-access__sidebar {
		z-index: 1000000;

		.spectra-ee-quick-access__sidebar--blocks {
			.spectra-ee-quick-access__sidebar--blocks--block {
				.spectra-ee-quick-access__sidebar--blocks--block--icon {
					&.spectra-ee-quick-access__sidebar-icon__core,
					&.spectra-ee-quick-access__sidebar-icon__wcfb,
					&.spectra-ee-quick-access__sidebar-icon__wpsp {
						svg {
							path,
							rect,
							circle {
								fill: #fff;
								stroke-width: 1.4px;
							}
						}
					}

					&.spectra-ee-quick-access__sidebar-icon__woocommerce {
						svg {
							path,
							rect,
							circle {
								fill: #fff;
								stroke-width: 1.4px;
							}
						}

						&--cart svg path:first-child,
						&--checkout svg path:first-child,
						&--mini-cart-contents svg path:first-child,
						&--product-top-rated svg path:first-child,
						&--store-notices svg path:first-child,
						&--featured-category svg path:first-child,
						&--active-filters svg path:first-child {
							fill: none;
						}
					}

					&.spectra-ee-quick-access__sidebar-icon__jetpack {
						&--gif path:last-child,
						&--map path:last-child,
						&--markdown path:last-child {
							fill: #fff;
						}
					}
				}
			}
		}
	}
}

.spectra-ee-quick-access__sidebar {
	&--blocks-sortable {
		display: flex;
		flex-direction: column;
		overflow: auto; /* Prevent horizontal scrolling */

		&--block {
			margin-bottom: 10px; /* Adjust as needed */
		}
	}

	&--prevent-editor-interactions {
		pointer-events: none;
	}
}